"use client"; // This directive indicates that this is a Client Component

import React, { useEffect, useState } from 'react';
import { Typewriter } from 'react-simple-typewriter';
import { Helmet } from 'react-helmet';

import ExpandableSection from './ExpandableSection'; // Ensure this is the correct import
import ExpandableSectionContent from './ExpandableSectionContent.json'; // Importing JSON content
import Loader from './Loader';

import './App.css'; // Adjust the path based on your project structure

const Home = () => {
  const [content, setContent] = useState(null); // Change to null to handle loading state
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Simulate a loading process
    const timer = setTimeout(() => {
      setLoading(false);
    }, 3000); // Loading for 3 seconds

    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    // Load the content from the JSON file
    setContent(ExpandableSectionContent); // Assuming the JSON structure is correct
  }, []);

  return (
    <main>
      <Helmet>
        <title>Andreas Kring</title>
      </Helmet>

      <Loader isLoading={loading} />

      <div className="min-h-screen w-full flex flex-col items-center justify-center p-4 font-serif">
        {/* Typewriter effect for welcome message */}
        <div className="w-full max-w-2xl my-60 text-xl font-mono text-gray-700 dark:text-white italic">
          <div className='w-full items-start justify-start px-4 md:px-8 lg:px-8'>
            {!loading && (
              <Typewriter
                words={['welcome to my portfolio', 'my projects just a mouse scroll away!']}
                loop={5}
                cursor
                cursorStyle='_'
                typeSpeed={70}
                deleteSpeed={20}
                delaySpeed={5000}
              />
            )}
          </div>
        </div>

        <div>
          {content && content.content.map((item, index) => (
            <ExpandableSection 
              key={index}
              title={item.title} 
              imagePath={item.imagePath} 
              resume={item.resume} 
              sections={item.sections} 
            />
          ))}
        </div>

        <div className='w-full max-w-2xl min-h-64 my-24 flex flex-col md:flex-row justify-between items-center'>
          <div className='w-full order-last md:order-first p-4 md:p-8'>
            <div className='w-full items-start justify-start px-2 pb-4 md:px-4 text-xl font-mono text-gray-700 dark:text-white italic'>
              {!loading && (
                <Typewriter
                  words={['who am I?', 'keep on scrolling']}
                  loop={5}
                  cursor
                  cursorStyle='_'
                  typeSpeed={70}
                  deleteSpeed={20}
                  delaySpeed={5000}
                />
              )}
            </div>
            <p>
              Just a page for me to tune in once in a while (when I feel for it) with some of the projects I find myself working on when I have time off from studying physics.
            </p>
          </div>
          <div className="w-full md:p-8">
            <img
              className="rounded-t-xl noselect"
              src='/profile.png'
              alt="Profile"
              width={2807} // Original width
              height={2807} // Original height
            />
            <div className='border-b border-gray-300 bg-white backdrop-blur-2xl dark:border-white dark:bg-zinc-800/30 text-xl'>
              <ul className='p-4'>
                <li>
                  <span className='font-bold pr-3'></span>
                  <span>Physics, UCPH</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
};

export default Home;
