import React, { useState, useRef, Suspense, lazy, useEffect } from 'react';
import { MathJax, MathJaxContext } from 'better-react-mathjax';

const KochSnowflake = lazy(() => import('./KochSnowflake'));
const Fjendulum = lazy(() => import('./Fjendulum'));
const ElasticPendulumSimulator = lazy(() => import('./ElasticPendulumSimulator'));

const componentMap = {
  KochSnowflake: KochSnowflake,
  ElasticPendulumSimulator: ElasticPendulumSimulator
};

const ExpandableSection = ({ title, imagePath, resume, sections }) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const sectionRef = useRef(null);

  const toggleContent = () => {
    setIsExpanded(!isExpanded);
  };

  useEffect(() => {
    if (window.MathJax && window.MathJax.typesetPromise) {
      window.MathJax.typesetPromise().catch((err) => {
        console.error('MathJax typeset error:', err);
      });
    }
  }, [isExpanded]);

  return (
    <MathJaxContext config={{
      loader: { load: ["input/tex", "output/chtml"] },
      options: { enableMenu: false }
    }}>
      <MathJax>
        <div ref={sectionRef} className="flex flex-col items-center py-6 overflow-hidden w-screen lg:max-w-2xl">
          <div className="w-full max-w-screen-sm sm:max-w-2xl border-b border-gray-300 rounded-xl bg-gradient-to-b from-zinc-200 dark:border-neutral-800 dark:bg-zinc-800/30 dark:from-inherit flex flex-col items-center">
            <div className="relative max-h-[30vh] overflow-hidden w-full">
              <img
                className="w-full h-auto rounded-t-xl noselect"
                src={imagePath}
                alt={title}
              />
              <div className="absolute bottom-0 left-0 px-4 lg:px-8 pb-4">
                <p className="font-serif font-bold text-3xl text-white">
                  {title}
                </p>
              </div>
            </div>
            <div className={`w-full max-w-full relative px-4 lg:px-8 py-4 transition-all duration-200 ${isExpanded ? 'pb-4' : 'pb-4'}`}>
              <p>{resume}</p>
              {isExpanded && (
                <div>
                  {sections.map((section, index) => (
                    <div key={index}>
                      <h3 className="font-bold text-lg pt-4">{section.subtitle}</h3>
                      {section.paragraphs.map((item, pIndex) => (
                        <div className="w-full" key={pIndex}>
                          {item.paragraph}
                        </div>
                      ))}
                      {section.component && componentMap[section.component] && (
                        <div className="w-full flex justify-center items-center">
                          <Suspense fallback={<div>Loading...</div>}>
                            {React.createElement(componentMap[section.component])}
                          </Suspense>
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              )}
              <div className="flex justify-center relative">
                <button
                  className={`absolute border-2 m-2 border-gray-300 dark:border-white rounded-lg
                  ${isExpanded ? '-top-8 border-none text-2xl' : '-top-11 border-b border-gray-300 dark:border-gray-300 p-1 bg-gray-100 dark:bg-zinc-900 text-md'}`}
                  onClick={toggleContent}
                >
                  {isExpanded ? '' : 'Read More'}
                </button>
              </div>
            </div>
          </div>
        </div>
      </MathJax>
    </MathJaxContext>
  );
};

export default ExpandableSection;
