// Loader.js
import React from 'react';

const Loader = ({ isLoading }) => {
  if (!isLoading) return null; // Don't render anything if not loading

  return (
    <div className='fixed z-10 w-full h-screen flex flex col items-center justify-center backdrop-blur-sm'
    >
      <div
        style={{
          width: '48px',
          height: '48px',
          borderRadius: '50%',
          display: 'inline-block',
          boxSizing: 'border-box',
          animation: 'spin 2s linear infinite',
          zIndex: '10'
        }}
      />
      <style>
        {`
          @keyframes spin {
            0% {
              box-shadow: -72px 0 rgb(var(--foreground-rgb)) inset;
            }
            100% {
              box-shadow: 48px 0 rgb(var(--foreground-rgb)) inset;
            }
          }
        `}
      </style>
    </div>
  );
};

export default Loader;
